import React, { Component } from 'react';
import { connect } from "react-redux";

import { moveStepTo, toolbarChanged, addPepehaEntry } from "../redux/actions";

import mapboxgl from 'mapbox-gl';

import '../styles/_map.scss';

import regionJSON from '../json/regions.json';
import mountainJSON from '../json/mountains.json';
import waterJSON from '../json/waters.json';
// import { thisTypeAnnotation } from '@babel/types';

const mapStateToProps = state => {
  return { state: state };
};

const mapDispatchToProps = dispatch => {
  return {
    moveStepTo: payload => dispatch(moveStepTo(payload)),
    toolbarChanged: payload => dispatch(toolbarChanged(payload)),
    addPepehaEntry: payload => dispatch(addPepehaEntry(payload)),
  };
}

class MapBoxEl extends Component {
  state = {
    active: false,
  }

  map;

  regionIcon = `<svg x="0px" y="0px" viewBox="0 0 120.4 118.7" style="enable-background:new 0 0 120.4 118.7;" xml:space="preserve"><path class="cls-1" d="M118.2,59.7L59.9,1.4L2.2,59l0,0l0,0l58.3,58.3L118.2,59.7L118.2,59.7L118.2,59.7z M60.5,109.1L16.4,65l87.8,0.5L60.5,109.1
    z"/></svg>`;

  mountainIcon = `<svg id="Isolation_Mode" data-name="Isolation Mode" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 120.41 73.74"><polygon class="cls-1" points="14.83 67.14 59.87 22.1 105.42 67.65 14.83 67.14"/><path class="cls-2" d="M59.87,30.6,90.82,61.55l-61.55-.34L59.87,30.6m0-17L.38,73.08,120,73.74,59.87,13.59Z"/></svg>`;

  waterIcon = `<svg id="Isolation_Mode" data-name="Isolation Mode" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 120.41 73.74"><path class="cls-1" d="M58.87,68.46A24.49,24.49,0,1,1,83.36,44,24.52,24.52,0,0,1,58.87,68.46Z"/><path class="cls-2" d="M58.87,24.77A19.2,19.2,0,1,1,39.67,44a19.22,19.22,0,0,1,19.2-19.2m0-10.57A29.77,29.77,0,1,0,88.65,44,29.77,29.77,0,0,0,58.87,14.2Z"/></svg>`;

  jsonList = {
    region: regionJSON,
    mountain: mountainJSON,
    water: waterJSON
  };
  
  iconList = {
    region: this.regionIcon,
    mountain: this.mountainIcon,
    water: this.waterIcon
  };

  mapBoxMarkers = [];
  currMarkerType = false;

  componentDidMount(){
    mapboxgl.accessToken = 'pk.eyJ1IjoiaGFtaXNoZHciLCJhIjoiY2pjcXo1M3F3MDRxZTMybnExeGpkZzdoYyJ9.I-j4YbF2mmPI3Xvzw6ZK4g';

    this.map = new mapboxgl.Map({
        container: 'map',
        style: 'mapbox://styles/hamishdw/cjkag7nws1txj2snsiguqlc7w',
        center: [174.160873, -40.842404],
        zoom: 5.3
    });

    this.map.on("load", () => {
      this.map.addControl(new mapboxgl.NavigationControl());

      this.map.setPaintProperty('water', 'fill-color', '#ffffff');
      this.map.setPaintProperty('background', 'background-color', '#dddddd');
      this.map.setPaintProperty('ne-10m-admin-1-states-provinc-9d4nfc', 'line-color', '#c2c2c2');

      this.resizeMap = this.map.resize.bind(this);

      window.addEventListener('resize', this.resizeMap);
    })
  }

  componentWillUnmount(){
    window.removeEventListener("resize", this.resizeMap);
  }

  componentDidUpdate(){
    const steps = {...this.props.state.moveStep},
          stageName = steps.simpleSteps[steps.simpleStep].MapBoxEl;

    if(this.state.active !== this.props.visible){
      this.setState({
        active: !this.state.active 
      })

      if(this.props.visible === true){
        this.props.toolbarChanged({buttonColor: 'white', burgerColor: 'white', bcgColor: 'black', mapDropdown: true});
      } else {
        this.props.toolbarChanged({buttonColor: 'black', burgerColor: 'black', bcgColor: 'transparent', mapDropdown: true});
      }
    }

    if(this.props.visible === true && stageName !== this.currMarkerType){
      this.props.toolbarChanged({buttonColor: 'white', burgerColor: 'white', bcgColor: 'black', mapDropdown: true});
      this.currMarkerType = stageName;
      this.mapBoxMarkers.forEach(marker => marker.remove());
      this.addLocations(this.jsonList[stageName], this.iconList[stageName]);
    }
  }

  addLocations(json,icon) {
    const currStep = this.props.state.moveStep.simpleStep;

    let dropdownNames = [],
        markerTemplate = document.createElement("div"),
        infoBox = document.createElement("div"),
        iconDiv = document.createElement("div"),
        name = document.createElement("p");

    markerTemplate.className = `mapBox-marker -step-${currStep}`;

    infoBox.className = "mapBox-marker-info";

    iconDiv.className = "mapBox-marker-icon";
    iconDiv.innerHTML = icon;
    
    name.className = "mapBox-marker-name";

    infoBox.appendChild(name);
    markerTemplate.appendChild(infoBox);
    markerTemplate.appendChild(iconDiv);

    this.mapBoxMarkers = [];

    json.forEach((marker, i) => {
      let el = markerTemplate.cloneNode(true),
          nameEl = el.querySelector(".mapBox-marker-name");

      el.id = 'marker-'+i;
      nameEl.innerHTML = marker.properties.name;

      if(marker.properties["name:mi"]){
        nameEl.setAttribute("data-maori", marker.properties["name:mi"]);
      } else {
        nameEl.setAttribute("data-maori", false);
      }

      // 4 === water
      if(currStep === 4 && marker.properties.natural){
        name.setAttribute("data-water", marker.properties.natural);
      }

      const mapBoxMarker = new mapboxgl.Marker(el)
          .setLngLat([marker.geometry.coordinates[0], marker.geometry.coordinates[1]])
          .addTo(this.map);

      this.mapBoxMarkers.push(mapBoxMarker);

      dropdownNames.push({
        "eng": marker.properties.name,
        "maori": marker.properties["name:mi"]
      })

      if(currStep === 4  && marker.properties.natural){
        dropdownNames[dropdownNames.length - 1].water = marker.properties.natural;
      }
    });

    this.setState({
      markers: true
    })

    this.props.moveStepTo({mapData: dropdownNames});

    setTimeout(() => {
      this.setMarkerEvents();
    },1000) // wait for preloader
  }

  setMarkerEvents(){
    this.markers = document.querySelectorAll(".mapBox-marker");

    // do this earlier, now that css vars used??
    this.mapZoom();
    this.map.on('zoom', () => {
      this.mapZoom();
    });

    this.map.on('mouseup', () => this.props.moveStepTo({mapSelection: false}));

    this.markers.forEach((marker, i) => {
      marker.addEventListener("click", () => {
        this.props.moveStepTo({mapSelection: this.props.state.moveStep.mapData[i]});
      })
    })
  }

  mapZoom(){
    const root = document.documentElement;

    let zoomVal = this.map.getZoom(),
        limit = 6;

    if(this.props.state.moveStep.simpleStep === 2){
      limit = 4;
    }

    if(zoomVal > limit){
      this.setState({
        names: true
      })

      let growth = 3;

      if(this.props.state.moveStep.simpleStep === 2){
        growth = 4.5;
      }

      let fontSize = (zoomVal * growth) - 18; // needs some tweaking

      root.style.setProperty('--markerInfo', fontSize+"px");
    } else if(this.state.names === true){
      this.setState({
        names: false
      })
    }

    let markerSize = (Math.pow(zoomVal, 2)); // needs some tweaking

    root.style.setProperty('--markerWidth', markerSize+"px");
    root.style.setProperty('--markerHeight', (markerSize * 0.333)+"px");
  }

  selected(){
    const steps = {...this.props.state.moveStep},
          stageName = steps.simpleSteps[steps.simpleStep].MapBoxEl,
          mapSelection = steps.mapSelection;

    this.props.addPepehaEntry({category: stageName, value: mapSelection, visible: true});
    this.props.moveStepTo({simpleStep: steps.simpleStep + 1, prevStep: "SimpleSteps", mapSelection: false});

    if(steps.simpleStep === 4){
      this.props.moveStepTo({currStep: "PepehaPortrait"});
      this.props.toolbarChanged({buttonColor: 'black', burgerColor: 'black', bcgColor: 'transparent', mapDropdown: false});
    }

    this.map.zoomTo(8, 700);
  }

  render() {
    const mapSelection = this.props.state.moveStep.mapSelection;

    return (
      <section className={`
        mapBox
        ${this.state.active ? '-active' : ''}
        ${this.state.markers ? '-markersActive' : ''}
        ${this.state.names ? '-namesActive' : ''}
      `}>
        <div id="map" className={`${mapSelection ? "-fade" : ""}`}/>

        <div className={`mapBox-selectionConfirm ${mapSelection ? "-active" : ""}`}>
          <p className="mapBox-selectionConfirm-text">{mapSelection ? mapSelection.eng : ""}</p>
          <p className="mapBox-selectionConfirm-button -nextText" onClick={(() => this.selected())}>
            <span>Next</span>
          </p>
        </div>
      </section>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(MapBoxEl);