import React, { useState } from 'react';

const Signup = ({active, close, done, loading}) => {
  const formEl = React.createRef(),
        submitEl = React.createRef();

  const [formValid, validityChanged] = useState(0);

  let checkedTruthy = false;

  const toggleRadio = e => {
    e.preventDefault();

    let checked = 1 - Number(e.target.getAttribute("data-radiochecked"));

    checkedTruthy = checked === 1 ? true : false;

    e.target.setAttribute("data-radiochecked", checked);

    if(checked){
      e.target.checked = true;
    } else {
      e.target.checked = false;
    }
  }

  const inputChanged = e => {
    const valid = formEl.current.checkValidity();
    
    validityChanged(valid);
  }

  return(
    // previosly pepehaDownload
    <div className={`pepehaPortrait-signup ${active ? "-active" : ""}`}>
      <svg className="pepehaPortrait-signup-back" x="0px" y="0px" viewBox="0 0 40.6 40.6" onClick={() => close()}>
        <circle className="st0" cx="20.3" cy="20.3" r="19.5"/>
        <polyline className="st0" points="24.1,28.1 16.5,20.3 24.1,12.7 	"/>
      </svg>

      <div className={`pepehaPortrait-signup-overlay ${loading ? "-active" : ""}`}>
        <img src="./loading.gif" alt="" className="pepehaPortrait-signup-overlay-loading"/>
      </div>

      <form 
        id="subForm" 
        className="pepehaPortrait-signup-form js-cm-form" 
        ref={formEl} data-id="A61C50BEC994754B1D79C5819EC1255C731DD779B1E59FBB65BE8BEACF7BB6824FD81C68318E5354E006E320B8323AF57370F028E283AA82CF1D188E46C73834"
        onSubmit={ e => {
          e.preventDefault();
          console.log('submitEl',submitEl);
          submitEl.current.click();
        }}
      >	
        <p className="pepehaPortrait-signup-title">Ka Rawe!</p>
        <p className="pepehaPortrait-signup-text -para1">Your Pepeha is ready.</p>
        
        <label htmlFor="fieldName" className="pepehaPortrait-signup-label">Full Name</label>
        <input className="pepehaPortrait-signup-input -name" id="fieldName" name="cm-name" type="text" placeholder="full name"  onChange={e => inputChanged(e)}/>
    
        <label htmlFor="fieldEmail" className="pepehaPortrait-signup-label">Email Address</label>
        <input id="fieldEmail" name="cm-whtkrl-whtkrl" type="email" className="js-cm-email-input pepehaPortrait-signup-input" required  placeholder="email address" onChange={e => inputChanged(e)}/>

        <div className="pepehaPortrait-signup-optIn">
          <input type="radio" id="cm-privacy-consent" name="cm-privacy-consent" data-radiochecked={"0"} className="pepehaPortrait-signup-optIn-radio" onClick={e => toggleRadio(e)}/>
          <label htmlFor="cm-privacy-consent" className="pepehaPortrait-signup-radioLabel">Opt in for pepeha updates</label>
          <input id="fieldjdyhtju" name="cm-f-jdyhtju" type="text" className="-hidden" />
        </div>

        <input id="fieldjdjyiki" name="cm-f-jdjyiki" type="text" className="-hidden" />
        <input id="fieldjdjyikd" name="cm-f-jdjyikd" type="text" className="-hidden" />

        <p 
          ref={submitEl}
          className={`pepehaPortrait-signup-button ${formValid ? "-valid" : ""}`} 
          onClick={() => done({
            fullName: document.querySelector("#fieldName").value,
            email: document.querySelector("#fieldEmail").value,
            subscribed: checkedTruthy,
          })}
        >{loading ? "LOADING" : "NEXT"}</p>

        <p className="pepehaPortrait-signup-agreeMessage agreeMessage">By clicking next you agree to the <a className="pepehaPortrait-signup-agreeLink" target="_blank" href="https://shop.pepeha.nz/pages/terms-and-conditions" rel="noopener noreferrer">terms and conditions</a></p>
        
        <button className="pepehaPortrait-signup-confirm js-cm-submit-button" type="submit">Subscribe</button>
      </form>
    </div>
  )
}

export default Signup;