import React, { Component } from 'react';
import { connect } from "react-redux";
import { Link } from 'react-router-dom';

import { moveStepTo, toolbarChanged } from "../redux/actions";

import NavMob from '../components/NavMob';

import '../styles/_topToolbar.scss';

const mapStateToProps = state => {
  return { state: state };
};

const mapDispatchToProps = dispatch => {
  return {
    moveStepTo: payload => dispatch(moveStepTo(payload)),
    toolbarChanged: payload => dispatch(toolbarChanged(payload))
  };
}

class TopToolbar extends Component {
  prevBtnClicked(){
    const steps = this.props.state.moveStep;

    let simpleStep = steps.simpleStep,
        prev = steps.prevStep;

    if(simpleStep !== 0){ // if not splash or advanced
      if(steps.currStep === "SimpleSteps"){
        simpleStep -= 1;
      } else if(steps.currStep === "PepehaPortrait"){
        simpleStep = 4;
      }
    } else {
      if(prev !== "SelectionMenu"){
        prev = "SplashPage";
      } else {
        this.props.moveStepTo({prevStep: "SplashPage"});
      } 
    }

    if(steps.addManually === true){
      this.props.moveStepTo({addManually: false});
      this.props.toolbarChanged({buttonColor: 'white', burgerColor: 'white', bcgColor: 'black', mapDropdown: true});
    } else {
      this.props.moveStepTo({currStep: prev, simpleStep: simpleStep});
    }

    this.input = document.querySelector(".floatingTextInput-input");

    if(this.input !== null) this.input.value = "";
  }

  nameClicked(dataNames){
      // let names = JSON.parse($(this).attr("data-names")),
      //     water = false;

      //       self.selectedName = {};
      //       self.selectedName.eng = names.eng;

      //       if($(this).attr("data-water")){
      //         water = $(this).attr("data-water");
      //         self.selectedName.water = water;
      //       }

      //       if(names.maori){
      //         self.selectedName.maori = names.maori;
      //       } else {
      //         self.selectedName.maori = false;
      //       }

      //       $(".simpleToolbar").removeClass("-open");
      //       $(".simpleToolbar-close").removeClass("-active");
      //       $(".simpleBack").addClass("-active");
      //       // self.selectionConfirmButtonClicked();

      //       if(self.stagesCompleted[self.currStage]){
      //         $(`.-${self.currStageName}.-selected`).removeClass("-selected");
      //       }

      //       $("#map").addClass("-fade");

      //       $(".selectionConfirm-text").html(names.eng);
      //       setTimeout(() => { // hotfix, for map click off
      //         $(".selectionConfirm").addClass("-active");
      //       },200)
  }

  render() {
    const state = this.props.state,
          toolbarChanged = state.toolbarChanged,
          steps = {...state.moveStep},
          stageName = steps.simpleSteps[steps.simpleStep] && steps.simpleSteps[steps.simpleStep].MapBoxEl,
          explanationDone = steps.explanationsShown[steps.simpleStep];

    let mapData = false;

    if(steps.mapData){
      mapData = [...steps.mapData].sort(function(a, b) {
        let textA = a.eng.toUpperCase();
        let textB = b.eng.toUpperCase();
        return (textA < textB) ? -1 : (textA > textB) ? 1 : 0;
      });
    }

    return (
      <section className={`
        topToolbar 
        ${toolbarChanged.active === true && "-active"} 
        -burger-${toolbarChanged.burgerColor}
        -bcg-${toolbarChanged.bcgColor}
        ${toolbarChanged.toolbarOpen === true && "-open"}
        ${toolbarChanged.clickable === false && "-unclickable"}
      `}>
        <svg 
          className={`topToolbar-back ${toolbarChanged.button === "back" && "-active"} -${toolbarChanged.buttonColor}`} 
          onClick={() => this.prevBtnClicked()}
          x="0px" y="0px" viewBox="0 0 40.6 40.6"
        >
          <circle className="cls-1" cx="20.3" cy="20.3" r="19.5"></circle>
          <polyline className="cls-1" points="24.1,28.1 16.5,20.3 24.1,12.7"></polyline>
        </svg>

        <Link to={state.moveStep.prevStep === null ? '/' : ''}>
          <svg 
            className={`topToolbar-close ${toolbarChanged.button === "close" && "-active"}`} 
            onClick={() => this.prevBtnClicked()}
            viewBox="0 0 40.58 40.58"
          >
            <circle className="cls-1" cx="20.29" cy="20.29" r="19.5"></circle>
            <line className="cls-1" x1="28.01" y1="12.57" x2="12.57" y2="28.01"></line>
            <line className="cls-1" x1="12.57" y1="12.57" x2="28.01" y2="28.01"></line>
          </svg>
        </Link>

        {mapData !== false && toolbarChanged.mapDropdown && explanationDone ? (
          <React.Fragment>
            {/* simpleToolbar */}
            <div className="topToolbar-selection" onClick={() => {
              this.props.toolbarChanged({toolbarOpen: !toolbarChanged.toolbarOpen})}
            }>
              <p className="topToolbar-selection-text">{"Select "+stageName}</p>
              <svg className="topToolbar-selection-chevron" viewBox="0 0 98.99 53.03"><g><polyline className="cls-1" points="97.2,2.8 49.5,50.5 1.8,2.8"/></g></svg>
            </div>

            <ul className="topToolbar-list">
              {mapData.map(names => {
                return(
                  <li 
                    key={names.eng}
                    className="topToolbar-list-item"
                    onClick={() => {
                      this.props.moveStepTo({mapSelection: names});
                      this.props.toolbarChanged({toolbarOpen: !toolbarChanged.toolbarOpen});
                    }}
                  >{names.eng}</li>
                )
              })}
            </ul>
            <p 
              className="topToolbar-cantFind"
              onClick={() => {
                this.props.toolbarChanged({toolbarOpen: !toolbarChanged.toolbarOpen});
                this.props.moveStepTo({addManually: true});
              }}
            >{`Can't find your ${stageName}?`}</p>
          </React.Fragment>
        ) : null}

        <NavMob/>
      </section>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(TopToolbar);