import React, { Component } from 'react';
import { connect } from "react-redux";

import { moveStepTo, toolbarChanged } from "../redux/actions";

import '../styles/_simpleSteps.scss';

import MapBoxEl from '../components/MapBoxEl';
import FloatingTextInput from '../components/FloatingTextInput';

const mapStateToProps = state => {
  return { state: state };
};

const mapDispatchToProps = dispatch => {
  return {
    moveStepTo: payload => dispatch(moveStepTo(payload)),
    toolbarChanged: payload => dispatch(toolbarChanged(payload))
  };
}

class SimpleSteps extends Component {
  state = {
    active: true,
    firstName: ''
  }

  questions = React.createRef();
  explanation = React.createRef();
  footer = React.createRef();

  componentWillMount(){
    this.setState({
      steps: this.props.state.moveStep
    })

    this.keyEvent = this.onKeyPress.bind(this);

    window.addEventListener("keypress", this.keyEvent)
  }

  componentWillUnmount(){
    window.removeEventListener("keypress", this.keyEvent);
  }

  componentDidMount(){
    this.setState({
      active: true
    })

    this.props.toolbarChanged({bcgColor: 'transparent'});
  }

  onKeyPress(e){
    let explanationDone = this.state.steps.explanationsShown[this.state.steps.simpleStep];
    if(e.which === 13 || e.keyCode === 13){
      if(this.state.active === true && explanationDone === false) this.nextSimpleStep();
    }
  }

  componentDidUpdate(){
    // if(this.state.active !== this.props.visible){
    //   this.setState({
    //     active: !this.state.active 
    //   })

    //   if(this.props.visible === true){
    //     this.props.toolbarChanged({buttonColor: 'black', burgerColor: 'black', bcgColor: 'transparent'});

    //     // console.log('this.props.state',this.props.state);
    //     // this.firstName = this.props.state
    //   }
    // }

    const steps = this.props.state.moveStep;

    if(
      this.state.steps.simpleStep !== steps.simpleStep
      ||
      this.state.steps.explanationsShown[this.state.steps.simpleStep] !== steps.explanationsShown[this.state.steps.simpleStep]
    ){
      this.setState({
        steps: steps
      })
    }
  }

  nextSimpleStep(){
    const steps = this.state.steps,
          explanationDone = steps.explanationsShown[this.state.steps.simpleStep];

    if(explanationDone === false){
      let explanationsArr = steps.explanationsShown;
      
      explanationsArr.splice(steps.simpleStep, 1, true);

      this.props.moveStepTo({
        explanationsShown: explanationsArr
      })
    } else {
      let prevStep = steps.prevStep;

      if(steps.simpleStep !== 0) prevStep = "simpleSteps";

      this.props.moveStepTo({
        simpleStep: steps.simpleStep + 1,
        prevStep: prevStep
      })
    }

    let firstName = this.props.state.updatePepehaData.name.entry;

    if(firstName !== undefined && firstName.eng !== this.state.firstName) this.setState({firstName: firstName.eng});
  }

  render() {
    const steps = this.state.steps,
          currStep = steps.simpleStep;

    const explanationDone = steps.explanationsShown[currStep],
          category = steps.simpleSteps[currStep].FloatingTextInput !== undefined ? steps.simpleSteps[currStep].FloatingTextInput : steps.simpleSteps[currStep].MapBoxEl,
          explanations = [
            {title: "Ingoa", para: "Ingoa means name, what is your name?"},
            {title: "Whānau", para: "Whānau means family, what is your family name?"},
            // Capitalise First name
            {title: "Nō hea koe?", para: `Where are you from ${this.state.firstName.charAt(0).toUpperCase() + this.state.firstName.substring(1)}?`, explanation: "region"},
            {title: "Maunga", para: "A Maunga is a mountain that is special to you or one that you have lived by.", explanation: "mountain"},
            {title: "Awa • Roto • Moana", para: "An Awa, Roto or Moana is a body of water that is special to you or one that you have lived by.", explanation: "awa, roto or moana"},
          ];

    return (
      <section className={`simpleSteps ${this.state.active ? '-active' : ''}`}>
        {/* prev simpleExplanation */}
        <div ref={this.explanation} className={`simpleSteps-explanation ${explanationDone !== "false" && "-active"}`}>
          <p className="simpleSteps-explanation-title">{explanations[currStep].title}</p>
          <p className="simpleSteps-explanation-text -para1">{explanations[currStep].para}</p>
          <p className={`simpleSteps-explanation-helpUs ${steps.simpleStep > 1 && "-active"}`}><b>Help us!</b> If your {explanations[currStep].explanation} isn't marked on the map provided, add it manually so we can build our&nbsp;database.</p>
          <p className="simpleSteps-explanation-next -nextText" onClick={() => this.nextSimpleStep()}><span>Next</span></p>
        </div>

        <MapBoxEl visible={explanationDone === true && steps.simpleStep >= 2 ? true : false}/>
        
        <FloatingTextInput 
          visible={
            (explanationDone === true && steps.simpleStep < 2) 
            || 
            this.props.state.moveStep.addManually
          }
          category={category ? category : ''}
          placeholder={
            category === "name" ? "first name" : 
            category === "family" ? "family name" : 
            category ? category : ''}
        />

        {/* prev simpleFooter */}
        <div ref={this.footer} className="simpleSteps-footer -active" style={{backgroundColor: this.props.state.toolbarChanged.bcgColor}}>
          <div className="simpleSteps-footer-contain">
            <p className="simpleSteps-footer-progress" style={{color: this.props.state.toolbarChanged.buttonColor}}>
              STEP {steps.simpleStep + 1} / 5
            </p>
            <p 
              className={`simpleSteps-footer-cantFind ${this.props.state.toolbarChanged.mapDropdown ? "-active" : ''}`}
              onClick={() => this.props.moveStepTo({addManually: true, mapSelection: false})}
            >Add manually</p>
          </div>
        </div>
      </section>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(SimpleSteps);