import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';

import { connect } from "react-redux";
import { toolbarChanged } from "../redux/actions";

import { TweenLite, Sine } from "gsap";

import Signup from '../components/Signup';
import Confirm from '../components/Confirm';

import createPdf from '../utils/createPdf';
import uploadData from '../utils/uploadData';

import '../styles/_pepehaPortrait.scss';

const mapStateToProps = state => {
  return { state: state };
};

const mapDispatchToProps = dispatch => {
  return {
    toolbarChanged: payload => dispatch(toolbarChanged(payload))
  };
}

const PepehaTextLine = ({self, visible = true, sentences, entry, simple = false}) => {
  if(visible === true){
    let maori, eng;

    if(simple){
      maori = "Kia ora tātou";
      eng = "greetings all";
    } else {
      maori = `${sentences.maori[0]} ${entry.maori ? entry.maori : entry.eng} ${sentences.maori[1]}`;
      eng = `${sentences.eng[0]} ${entry.eng} ${sentences.eng[1]}`;
    }
    
    return(
      <React.Fragment>
        <h3 
          style={{fontSize: self.state.textSizes.maori + "px"}}
          className="pepehaPortrait-maori"
          dangerouslySetInnerHTML={ {__html: self.splitIntoSpans(maori)} }
        />

        <p 
          style={{fontSize: self.state.textSizes.eng + "px"}}
          className="pepehaPortrait-eng"
          dangerouslySetInnerHTML={ {__html: self.splitIntoSpans(eng)} }
        />
      </React.Fragment>
    )
  } else return null
}

class PepehaPortrait extends Component {
  state = {
    active: false,
    pepehaData: {},
    pepehaStyles: {},
    textSizes: {
      eng: null,
      maori: null
    },
    containerScale: 1,
    theme: "white",
    previewIsClosed: true,
    showFinBtn: false,
    ios: false,
    isSafari: false,
    confirmActive: false,
    signUpActive: false,
    portraitReady: false
  }

  settings = {
    paperRatio: 1.41,
    
    engEm: 0.73,
    maoriEm: 2.4,

    maxWRatio: 0.75,
    maxHRatio: 0.80,

    previewTiming: 0.15,
  }

  el = {
    preview: React.createRef(),
    container: React.createRef(),
    zoomIcon: React.createRef(),
    cornerClose: React.createRef(),
    confirm: React.createRef(),
  }

  instagramMinFontSize = 8;

  componentDidMount(){
    this.previewBox= this.el.preview.current.getBoundingClientRect();
    this.emSize = this.previewBox.width / 100;

    if(this.emSize * this.settings.engEm < this.instagramMinFontSize){
      let newEmSize = this.instagramMinFontSize / this.settings.engEm,
          emDiff = newEmSize / this.emSize;

      this.emSize = newEmSize;
      this.defaultScale = emDiff;
    }

    // this.el.preview.current.style.fontSize = this.emSize + "px";
    // this.el.preview.current.style.height = this.previewBox.width * this.settings.paperRatio + "px";
    document.querySelector(".pepehaPortrait-colors").style.width = this.previewBox.width + "px";

    // console.log('this.el.preview.current.style.height',this.el.preview.current.style.height);

    // this.previewBox = this.el.preview.current.getBoundingClientRect();

    // console.log('this.previewBox.height',this.previewBox.height);

    this.setState({
      pepehaStyles: {
        ...this.state.pepehaStyles,
        transform: `scale(${1 / this.defaultScale}) translate(-50%,-50%)`,
        width: this.previewBox.width * this.defaultScale,
        maxWidth: this.previewBox.width * this.defaultScale,
        height: this.previewBox.width * this.settings.paperRatio * this.defaultScale,
        fontSize: this.emSize + "px"
      },
      textSizes: {
        eng: (this.emSize * this.settings.engEm).toFixed(4),
        maori: (this.emSize * this.settings.maoriEm).toFixed(4),
      }
    })

    this.resizeEvent = this.onResize.bind(this);
    this.orientationEvent = this.onOrientationChange.bind(this);

    window.addEventListener("resize", this.resizeEvent);
    window.addEventListener("orientationChange", this.resizeEvent);
    window.addEventListener("click", this.bodyClicked.bind(this));

    this.windowW = window.innerWidth;
    this.windowH = window.innerHeight;

    if(this.windowW > 1024){
      this.isDektop();
    }
  }

  componentWillUnmount(){
    window.removeEventListener("resize", this.resizeEvent);
    window.removeEventListener("orientationChange", this.resizeEvent);
  }

  onResize(){
    this.windowW = window.innerWidth;
    this.windowH = window.innerHeight;

    // this.defaultResizing();
    this.setSizesForPortraitPreview();

    // setTimeout(() => {
    //   this.scaleSvgPreview();
    // }, 1000)
  }

  onOrientationChange(){
    // this.defaultResizing();
  }

  setSizesForPortraitPreview(){
    let delay = 0;

    if(this.state.previewIsClosed === false){
      this.zoomIn();
      delay = 700;
    } 

    setTimeout(() => {
      this.setState({
        pepehaStyles: { }
      })
  
      this.previewBox = this.el.preview.current.getBoundingClientRect();
      this.emSize = this.previewBox.width / 100;
  
      if(this.emSize * this.settings.engEm < this.instagramMinFontSize){
        let newEmSize = this.instagramMinFontSize / this.settings.engEm,
            emDiff = newEmSize / this.emSize;
  
        this.emSize = newEmSize;
        this.defaultScale = emDiff;
      }
  
      document.querySelector(".pepehaPortrait-colors").style.width = this.previewBox.width + "px";
  
      this.setState({
        pepehaStyles: {
          ...this.state.pepehaStyles,
          transform: `scale(${1 / this.defaultScale}) translate(-50%,-50%)`,
          width: this.previewBox.width * this.defaultScale,
          maxWidth: this.previewBox.width * this.defaultScale,
          height: this.previewBox.width * this.settings.paperRatio * this.defaultScale,
          fontSize: this.emSize + "px"
        },
        textSizes: {
          eng: (this.emSize * this.settings.engEm).toFixed(4),
          maori: (this.emSize * this.settings.maoriEm).toFixed(4),
        }
      })
    }, delay)
  }

  defaultResizing(){
    if(this.emSize * this.settings.engEm < this.instagramMinFontSize){
      let newEmSize = this.instagramMinFontSize / this.settings.engEm;

      this.emSize = newEmSize;
    }

    this.previewBox = this.el.preview.current.getBoundingClientRect();

    document.querySelector(".pepehaPortrait-colors").style.width = this.previewBox.width + "px";

    this.setState({
      pepehaStyles: {
        ...this.state.pepehaStyles,
        height: this.previewBox.width * this.settings.paperRatio / (1 / this.defaultScale),
        fontSize: this.emSize
      },
      textSizes: {
        eng: (this.emSize * this.settings.engEm).toFixed(4),
        maori: (this.emSize * this.settings.maoriEm).toFixed(4),
      }
    })
  }

  componentDidUpdate(){
    this.payload = {...this.state};

    // zoomed in
    if(this.payload.previewIsClosed === false){
      // let portraitDimensions = document.querySelector(".pepehaPortrait-preview").getBoundingClientRect();

      // document.querySelector(".pepehaPortrait-preview").scrollTop = portraitDimensions.height * this.payload.previewClickPos.y;

      // setTimeout(() => {
      //   window.scrollTo(portraitDimensions.height * this.payload.previewClickPos.y, portraitDimensions.width * this.payload.previewClickPos.x);
      // }, 500)
    }

    let changed = false;

    if(this.state.active !== this.props.visible){
      changed = true;
      this.payload.active = !this.state.active; 
    }

    if(this.state.pepehaData !== this.props.state.updatePepehaData){
      changed = true;
      this.payload.pepehaData = this.props.state.updatePepehaData;
    }

    if(changed){
      this.setState(this.payload);

      let dataCount = Object.keys(this.payload.pepehaData).filter(key => this.payload.pepehaData[key].visible === true).length;

      if(this.payload.active === true && dataCount > 0){
        this.scaleSvgPreview();
        this.setState({showFinBtn : true});
      } else {
        this.setState({showFinBtn : false});
      }
    }
  }

  scaleSvgPreview(){
    this.el.container.current.style.transform = `scale(${1}) translate(-50%,-50%)`;
    
    document.querySelectorAll(".pepehaPortrait-maori").forEach(maoriText => {
      maoriText.style.width = "auto";
    })

    let frameW = Math.round(this.el.preview.current.offsetWidth),
        initialPepehaW = Math.round(this.el.container.current.offsetWidth),
        initialPepehaH = Math.round(this.el.container.current.offsetHeight);

    let frameH = Math.round(frameW * this.settings.paperRatio); // w:h scale A3

    this.scale = ((frameW * this.settings.maxWRatio) / initialPepehaW);

    if((frameH * this.settings.maxHRatio) / initialPepehaH < this.scale){
      this.scale = ((frameH * this.settings.maxHRatio) / initialPepehaH);
    }

    this.el.container.current.style.transform = `scale(${this.scale}) translate(-50%,-50%)`;

    let maxWidth = this.el.preview.current.offsetWidth;

    if(this.el.container.current.offsetWidth < maxWidth){
      let fillWidth = maxWidth / this.scale * this.settings.maxWRatio;

      document.querySelector(".pepehaPortrait-maori").style.width = fillWidth;
    } else {
      document.querySelector(".pepehaPortrait-maori").style.width = "auto";
    }
  }

  splitIntoSpans(string){
    let words = string.split(" ").filter(word => word.length > 0),
        spanString = '';
    
    words.forEach((word, i) => {
      if(i === words.length - 1){
        if(!this.state.isSafari){
          spanString += "<em class='nbsp'>&nbsp;</em>";
        } else {
          spanString += "<span class='nbsp'>&nbsp;</span>";
        }
      } else if(i !== 0){
        spanString += ' ';
      }

      spanString += `<span>${word}</span>`
    })

    let deDashed = spanString.replace(/-/g, '</span><span>-</span><span>');

    return deDashed;
  }

  zoomIn(e){
    if(this.state.previewIsClosed === true){
      this.preZoomTransform = this.state.pepehaStyles.transform;
      this.previewBox = this.el.preview.current.getBoundingClientRect();

      let x = e.nativeEvent.offsetX,
          y = e.nativeEvent.offsetY;

      let scale = 1 / this.defaultScale,
          scaledDimensions = {width: this.previewBox.width / scale, height: this.previewBox.height / scale},
          coOrdsPercent = {x: x / scaledDimensions.width, y: y / scaledDimensions.height};

      if(this.windowW < 1025 || this.state.ios){
        let scaleSize = (this.windowW * 1.5) / scaledDimensions.width;

        this.setState({
          previewIsClosed: false,
          previewClickPos: coOrdsPercent,
          pepehaStyles: {
            ...this.state.pepehaStyles,
            transform: `scale(${scaleSize}) translate(0,0)`
          },
        })
      } else {
        let scaleSize = (this.windowH * 2) / scaledDimensions.height,
            checkMaxWidth = (this.windowW * 0.75) / scaledDimensions.width;

        if(scaleSize > checkMaxWidth) scaleSize = checkMaxWidth;

        this.setState({
          previewIsClosed: false,
          pepehaStyles: {
            ...this.state.pepehaStyles,
            transform: `scale(${scaleSize}) translate(-50%,0)`
          },
        })
      }
    } else {
      const pepehaPortrait = document.querySelector(".pepehaPortrait");

      pepehaPortrait.scrollTop = 0; // for Edge
      pepehaPortrait.scrollLeft = 0; // for orientation change on iPad pro

      setTimeout(() => {
        this.setState({
          previewIsClosed: true,
          pepehaStyles: {
            ...this.state.pepehaStyles,
            transform: this.preZoomTransform
          },
        })
      }, 100)
    }

    document.querySelector(".mobileAdjustBox").classList.toggle("-zoomed");
  }

  isDektop(){
    this.zoomIconWidth = this.el.zoomIcon.current.offsetWidth;
  }

  previewMouseEnter(){
    this.zoomIconMoving = true;

    TweenLite.to(this.el.zoomIcon.current, this.settings.previewTiming, {opacity: 0, ease: Sine.easeIn, onComplete: () => {
      this.zoomIconMoving = false;
      TweenLite.to(this.el.zoomIcon.current, this.settings.previewTiming, {opacity: 1, ease: Sine.easeIn})
    }})

    this.el.cornerClose.current.classList.add('-hide');
  }

  previewMouseMove(e){
    if(this.zoomIconMoving === true) return;

    let x = e.nativeEvent.offsetX,
        y = e.nativeEvent.offsetY;

    TweenLite.to(this.el.zoomIcon.current, 0, {x: x - (this.zoomIconWidth / 2) + "px", y: y - (this.zoomIconWidth / 2) + "px"})
  }

  previewMouseLeave(){
    TweenLite.to(this.el.zoomIcon.current, this.settings.previewTiming, {opacity: 0, ease: Sine.easeIn, onComplete: () => {
      this.el.zoomIcon.current.style.transform = '';
      TweenLite.to(this.el.zoomIcon.current, this.settings.previewTiming, {opacity: 1, ease: Sine.easeIn})
    }})

    this.el.cornerClose.current.classList.remove('-hide');
  }

  openFinishedWindow(){
    this.setState({
      confirmActive: true
    })
  }

  closeFinishedWindow(){
    this.setState({
      confirmActive: false
    })
  }

  openSignup(){
    this.props.toolbarChanged({active: false, button: false});

    this.setState({
      signUpActive: true
    })
  }

  closeSignup(){
    this.props.toolbarChanged({active: true, button: "back"});

    this.setState({
      signUpActive: false
    })
  }

  uploadPortraitAndData(userData){
    this.setState({loading: true});

    createPdf(this).then(portraitData => {
      uploadData(userData, portraitData)
      .then(response => {
        this.setState({loading: false, portraitReady: true});
        console.log('data uploaded!', response);
      }).catch(err => {
        console.log('err',err);
        this.setState({loading: false});
      });
    }).catch(err => {
      console.log('err',err);
      this.setState({loading: false});
    });
  }

  bodyClicked(e){
    if(this.state.confirmActive){
      let targetClass = e.target.classList.value,
          checkConfirm = targetClass.indexOf("confirm"),
          checkSignup = targetClass.indexOf("signup"),
          checkOpenButton = e.target.closest(".pepehaPortrait-next-button");

      if(checkConfirm === -1 && checkSignup === -1 && checkOpenButton === null){
        this.setState({
          confirmActive: false,
          signUpActive: false
        })

        this.props.toolbarChanged({active: true, button: "back"});
      }
    }
  }

  render() {
    if (this.state.portraitReady === true) {
      return <Redirect to='./portrait-ready' />
    }

    return (
      <section className={`pepehaPortrait ${this.state.active ? '-active' : ''}`}>
        {/* // prev pepehaSvg */}
        <p className="pepehaPortrait-title">Your Pepeha</p>

        <div className="pepehaPortrait-leftBorder">
          <svg className="pepehaPortrait-leftBorder-triangle" viewBox="0 0 16.6 30" x="0px" y="0px">
            <polyline className="st0" points="-0.3,30.6 15.4,15 -0.3,-0.6 "/>
          </svg>
        </div>

        <div 
          className={`pepehaPortrait-next ctaButton ${this.state.showFinBtn ? "-active" : ""}`}
          onClick={() => this.openFinishedWindow()}
        >
          <div className="ctaButton-ripple"></div>
          <svg className="pepehaPortrait-next-button ctaButton-button" x="0px" y="0px" viewBox="0 0 73.6 73.6">
            <circle className="st0" cx="36.8" cy="36.8" r="36.8"/>
            <path d="M14.3,34.5H11v1.7h2.7v1.3H11V41H9.6v-7.8h4.7V34.5z"/>
            <path d="M17.8,41h-1.4v-7.8h1.4V41z"/>
            <path d="M27,41h-1.1l-4-5.1V41h-1.4v-7.8h1.2l3.9,5.1v-5.1H27V41z"/>
            <path d="M31.2,41h-1.4v-7.8h1.4V41z"/>
            <path d="M35.8,41c-1.1,0-2-0.6-2.6-1.5l1.2-0.7c0.3,0.5,0.9,0.8,1.4,0.8c0.7,0,1.2-0.4,1.2-0.8s-0.2-0.8-1.2-1.2l-0.2-0.1
              c-0.8-0.3-2.1-0.8-2.1-2.3c0.1-1.2,1.1-2.2,2.3-2.1c0.9,0,1.7,0.4,2.2,1.1L36.8,35c-0.2-0.3-0.6-0.5-1-0.5c-0.6,0-0.9,0.3-0.9,0.8
              s0.2,0.7,1.2,1.1l0.2,0.1c1.2,0.5,2.1,1.1,2.1,2.4C38.3,40.1,37.2,41.1,35.8,41C35.9,41.1,35.8,41.1,35.8,41z"/>
            <path d="M47.1,41h-1.4v-3.4H42V41h-1.4v-7.8H42v3h3.7v-3h1.4L47.1,41z"/>
            <path d="M54.6,41h-4.7v-7.8h4.7v1.3h-3.3v1.7H54v1.3h-2.7v2.1h3.3V41z"/>
            <path d="M59.5,41h-2.5v-7.8h2.8c2.1,0,3.8,1.7,3.9,3.8c0,0,0,0.1,0,0.1c0,2.2-1.8,3.9-3.9,3.9C59.6,41,59.5,41,59.5,41z M59.6,34.6
              h-1.3v5.1h1c1.4,0.1,2.7-1,2.8-2.5c0,0,0,0,0-0.1c0-1.4-1-2.5-2.4-2.6C59.7,34.6,59.7,34.6,59.6,34.6L59.6,34.6z"/>
          </svg>
        </div>
        <div 
          className={`pepehaPortrait-preview -theme-${this.state.theme}`} 
          ref={this.el.preview} 
          style={this.state.pepehaStyles}
          onClick={e => this.zoomIn(e)}
          onMouseEnter={() => this.previewMouseEnter()}
          onMouseMove={e => this.previewMouseMove(e)}
          onMouseLeave={() => this.previewMouseLeave()}
        >
          <div className="pepehaPortrait-frame" data-html2canvas-ignore/>
          <div className="pepehaPortrait-container" ref={this.el.container}>
            {this.props.state.moveStep.stage === "simple" ? (
              <PepehaTextLine self={this} simple={true}/>
            ) : null}

            {Object.keys(this.state.pepehaData).map(key => {
              const item = this.state.pepehaData[key];
              
              return(
                <PepehaTextLine self={this} key={key} visible={item.visible} sentences={item.sentences} entry={item.entry}/>
              )
            })}
          </div>
          <div className="pepehaPortrait-zoom" ref={this.el.zoomIcon}>
            <svg className="pepehaPortrait-zoom-icon" viewBox="0 0 40.58 40.58">
              <circle className="cls-1" cx="20.29" cy="20.29" r="19.5"></circle>
              <line className="cls-1" x1="28.01" y1="12.57" x2="12.57" y2="28.01"></line>
              <line className="cls-1" x1="12.57" y1="12.57" x2="28.01" y2="28.01"></line>
            </svg>
          </div>
        </div>

        <svg className="pepehaPortrait-zoom-cornerClose" viewBox="0 0 40.58 40.58" ref={this.el.cornerClose} onClick={() => this.zoomIn()}>
          <circle className="cls-1" cx="20.29" cy="20.29" r="19.5"></circle>
          <line className="cls-1" x1="28.01" y1="12.57" x2="12.57" y2="28.01"></line>
          <line className="cls-1" x1="12.57" y1="12.57" x2="28.01" y2="28.01"></line>
        </svg>

        <div className="pepehaPortrait-colors">
          <div className="pepehaPortrait-colors-container">
            <div className="pepehaPortrait-color -white -active" onClick={() => this.setState({theme: "white"})}>
              <div className="pepehaPortrait-color-dot">M&#x0100;</div>
            </div>
            
            <div className="pepehaPortrait-color -black" onClick={() => this.setState({theme: "black"})}>
              <div className="pepehaPortrait-color-dot">PANGO</div>
            </div>
          </div>
        </div>

        <Confirm open={this.state.confirmActive} close={this.closeFinishedWindow.bind(this)} openSignUp={this.openSignup.bind(this)}/>

        <Signup active={this.state.signUpActive} close={this.closeSignup.bind(this)} done={this.uploadPortraitAndData.bind(this)} loading={this.state.loading}/>
      </section>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(PepehaPortrait);