import * as jsPDF from 'jspdf'
import base64Fonts from './base64Fonts.json';
// import pdfjsLib from "pdfjs-dist/build/pdf";
import { pdfjs } from 'react-pdf';

// const pdfSWLocation = (window.location.hostname === "localhost" || window.location.hostname !== "192.168.0.123") ? `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js` : window.location.origin + `/pdf.worker.js`;

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

export default function createPdf(self){
  return new Promise((resolve, reject) => {
    let a3W = 297,
        a3H = 420;

    const callAddFont = function(){
      this.addFileToVFS('PepehaDisplay.ttf', base64Fonts.pepehaBase64);
      this.addFont('PepehaDisplay.ttf', 'PepehaDisplay', 'normal');

      this.addFileToVFS('AndSansRegular.ttf', base64Fonts.andSansBase64);
      this.addFont('AndSansRegular.ttf', 'AndSansRegular', 'normal');
    };

    jsPDF.API.events.push(['addFonts', callAddFont]);

    const mmToPoint = 0.352778;

    const pepehaPDF = new jsPDF({
      format: [a3W / mmToPoint, a3H / mmToPoint]
    });

    const previewBounds = self.el.preview.current.getBoundingClientRect();

    let frameW = self.el.preview.current.offsetWidth / self.defaultScale;
    let frameH = frameW * self.settings.paperRatio; // w:h scale A3
    let frameTop = previewBounds.top,
        htmlMaori = self.el.container.current.querySelectorAll(".pepehaPortrait-maori"),
        htmlEng = self.el.container.current.querySelectorAll(".pepehaPortrait-eng"),
        htmlMaoriFontsize = Number(self.state.textSizes.maori),
        htmlEngFontsize = Number(self.state.textSizes.eng),
        svgMaoriFontsize,
        svgEngFontsize,
        htmlToSvgHRatio = a3H / frameH,
        colors = [
          () => {pepehaPDF.setTextColor(0, 0, 0); pepehaPDF.setFillColor(255, 255, 255)},
          () => {pepehaPDF.setTextColor(218, 211, 197); pepehaPDF.setFillColor(0, 0, 0)}
        ];

    svgMaoriFontsize = ((htmlMaoriFontsize * self.scale * htmlToSvgHRatio) / mmToPoint) /  self.defaultScale;
    svgEngFontsize = ((htmlEngFontsize * self.scale * htmlToSvgHRatio) / mmToPoint) /  self.defaultScale;

    if(self.state.theme === "white"){
      colors[0]();
    } else {
      colors[1]();
    }

    pepehaPDF.setDrawColor(0);
    pepehaPDF.rect(0, 0, a3W, a3H, 'F');

    self.trackLines = [];

    htmlMaori.forEach(text => {
      const lines = splitIntoLines(text, frameTop, htmlToSvgHRatio);

      self.trackLines.push(lines);

      lines.forEach(line => {
        pepehaPDF.setFont("PepehaDisplay", 'normal');
        pepehaPDF.setFontSize(svgMaoriFontsize);

        const letterSpacing = svgMaoriFontsize * 0.1,
              extraSpacingFromLetterSpacing = (line.text.replace(/ /g,"").length - 3) * letterSpacing;

        pepehaPDF.text(line.text, ((a3W / 2) - (extraSpacingFromLetterSpacing / 2)), line.y, {align: "center", charSpace: letterSpacing});
      })
    })

    htmlEng.forEach(text => {
      const lines = splitIntoLines(text, frameTop, htmlToSvgHRatio);

      self.trackLines.push(lines);

      lines.forEach(line => {
        pepehaPDF.setFont("AndSansRegular", 'normal');
        pepehaPDF.setFontSize(svgEngFontsize);

        const letterSpacing = svgEngFontsize * 0.19,
              extraSpacingFromLetterSpacing = (line.text.replace(/ /g,"").length - 3) * letterSpacing;

        pepehaPDF.text(line.text, ((a3W / 2) - (extraSpacingFromLetterSpacing / 2)), line.y, {align: "center", charSpace: letterSpacing});
      })
    })

     const pdfOutput = pepehaPDF.output();

    createJpg(pdfOutput).then(img => {
      resolve({pdf: pdfOutput, img: img, trackLines: self.trackLines});
    }).catch(err => reject(err));
  })
}

const splitIntoLines = (text, frameTop, htmlToSvgHRatio) => {
  const spans = text.querySelectorAll("span:not(.nbsp)");

  let lines = [];

  spans.forEach(span => {
    let spanBounds = span.getBoundingClientRect(),
        y = (spanBounds.top + (spanBounds.height * 0.75) - frameTop) * htmlToSvgHRatio,
        extractedText = span.innerText.toUpperCase();

    if(extractedText !== "" && extractedText !== " "){
      if(lines.length < 1 || lines[lines.length - 1].y !== y){ // if first or on new line
        lines.push({
          y: y,
          text: extractedText
        })
      } else {
        lines[lines.length - 1].text += " " + extractedText;
      }
    }
  })

  // remove space between dashes
  lines = lines.map(line => {
    return {
      ...line,
      text: line.text.replace(/( |)-( |)/g, '-') // regex = space or nothing on both ends, with a dash between
    }
  });

  return lines;
}

const createJpg = pdfOutput => {
  return new Promise((resolve, reject) => {
    let loadingTask = pdfjs.getDocument({data: pdfOutput});
 
    loadingTask.promise.then(pdf => {
      pdf.getPage(1).then(page => {
        let scale = 0.9;
        let viewport = page.getViewport(scale);
        let canvas = document.createElement("canvas");
        let context = canvas.getContext('2d');

        canvas.height = viewport.height;
        canvas.width = viewport.width;

        let renderContext = {
          canvasContext: context,
          viewport: viewport
        };

        let renderTask = page.render(renderContext);

        renderTask.then(() => {
          let imgSrc = canvas.toDataURL("image/jpeg");
          resolve(imgSrc);

        }).catch(err => reject(err));
      });
    }, err => reject(err));
  })
}