import React, { Component } from 'react';
import { connect } from "react-redux";

import { moveStepTo, addPepehaEntry, toolbarChanged } from "../redux/actions";

import '../styles/_floatingTextInput.scss';

const mapStateToProps = state => {
  return { state: state };
};

const mapDispatchToProps = dispatch => {
  return {
    moveStepTo: payload => dispatch(moveStepTo(payload)),
    addPepehaEntry: payload => dispatch(addPepehaEntry(payload)),
    toolbarChanged: payload => dispatch(toolbarChanged(payload))
  };
}

class FloatingTextInput extends Component {
  state = {
    active: false
  }

  inputEl = React.createRef();
  prevStep = 0;
  isIos = false;

  componentDidMount(){
    if(this.props.state.deviceInfo.os === "iOS"){
      this.isIos = true;
    } else {
      window.addEventListener("resize", () => this.windowResize());
    }
  }

  setViewportHeightMobile(innerH = window.innerHeight){
    let vh = innerH * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
  }

  componentDidUpdate(){
    if(this.state.active !== this.props.visible){
      this.setState({
        active: !this.state.active 
      })

      if(this.props.visible === true){
        this.focused();

        const entry = this.props.category ? this.props.state.updatePepehaData[this.props.category].entry : undefined;

        if(entry !== undefined){
          this.setState({
            value: entry.eng
          })
        } else {
          this.setState({
            value: ''
          })
        }

        this.inputEl.current.focus();

        if(this.props.state.moveStep.stage === "simple"){
          this.props.toolbarChanged({buttonColor: 'black', burgerColor: 'black', bcgColor: 'transparent', mapDropdown: false});
        }
      }
    }

    const currStep = this.props.state.moveStep.simpleStep;

    if(currStep !== this.prevStep && this.props.visible === true){
      this.prevStep = currStep;

      const entry = this.props.category ? this.props.state.updatePepehaData[this.props.category].entry : undefined;

      if(entry !== undefined){
        this.setState({
          value: entry.eng
        })

        this.inputEl.current.focus();
      }

      // setTimeout(() => {
      //   console.log('entry',entry);
      //   console.log('this.state.value',this.state.value);
      // }, 1000)
    }
  }

  keyPressed(e){
    if((e.which === 13 || e.keyCode === 13) && e.target.checkValidity()){
      this.addPepehaEntry(e);
    }
  }

  addPepehaEntry(e){
    e.target.blur();

    const steps = this.props.state.moveStep;
          
    this.props.addPepehaEntry({category: this.props.category, value: {eng: this.state.value, maori: false}, visible: true});

    let stepPayload = { addManually: false }

    if(steps.stage === "simple"){
      if(steps.simpleStep === 4){
        stepPayload = { ...stepPayload, simpleStep: steps.simpleStep + 1, prevStep: "SimpleSteps", currStep: "PepehaPortrait"}
        this.props.toolbarChanged({buttonColor: 'black', burgerColor: 'black', bcgColor: 'transparent', mapDropdown: false});
      } else {
        stepPayload = { ...stepPayload, simpleStep: steps.simpleStep + 1, prevStep: "SimpleSteps"}
      }
    }

    this.props.moveStepTo( stepPayload );

    // this.inputEl.current.blur();
  }

  windowResize(){
    // if(!this.state.active) return;

    this.setViewportHeightMobile();
  }

  focused(){
    this.inputEl.current.scrollIntoView();

    setTimeout(() => {
      if(this.isIos){
        let innerH = document.documentElement.clientHeight * 0.5
        this.setViewportHeightMobile(innerH);
      }
    }, 100)
  }

  blurred(){
    setTimeout(() => {
      if(this.isIos){
        this.setViewportHeightMobile();
      }
    }, 100)
  }
  
  // Previously selectionOverlay
  render() {
    const stepCount = this.props.state.moveStep.simpleStep;

    return (
      <section className={`floatingTextInput ${this.state.active ? '-active' : ''}`}>
        <p className={`floatingTextInput-macrons ${stepCount > 1 ? '-active' : ''}`}>Don't forget your macrons. To activate your macrons, hold down the vowel key and a set of macrons will appear.</p>

        <div className="floatingTextInput-container">
          <input 
            type="text" className="floatingTextInput-input" required
            ref={this.inputEl}
            placeholder={this.props.placeholder}
            onKeyUp={e => this.keyPressed(e)}
            onChange={e => this.setState({value: e.target.value})}
            value={this.state.value}
            onFocus={() => this.focused()}
            onBlur={() => this.blurred()}
          />
          <div className="floatingTextInput-input-dummyCursor"></div>

          <p className="floatingTextInput-button nextButton -nextText" onClick={e => this.addPepehaEntry(e)} data-stage="">
            <span>Next</span>
          </p>
        </div>
      </section>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(FloatingTextInput);