import React, { Component } from 'react';

import TopToolbar from '../components/TopToolbar';
import SplashPage from '../components/SplashPage';
import SimpleSteps from '../components/SimpleSteps';
import PepehaPortrait from '../components/PepehaPortrait';
import SelectionMenu from '../components/SelectionMenu';
import PleaseUpdate from '../components/PleaseUpdate';

import { connect } from "react-redux";
import { resetPepehaData, resetPosition, moveStepTo } from "../redux/actions";

const mapStateToProps = state => {
  return { state: state };
};

const mapDispatchToProps = dispatch => {
  return {
    resetPepehaData: payload => dispatch(resetPepehaData(payload)),
    resetPosition: payload => dispatch(resetPosition(payload)),
    moveStepTo: payload => dispatch(moveStepTo(payload)),
  };
}

class CreatePepeha extends Component {
  state = {
    visible: true
  }

  componentDidMount(){
    const OS = this.props.state.deviceInfo.os;
    if(OS === "iOS" || OS === "Android OS"){
      console.log('is mobile');
    }

    if(document.readyState === "complete"){
      this.onLoad();
    } else {
      window.addEventListener("load", () => this.onLoad());
    }

    this.setState({
      windowW: window.innerWidth
    })

    this.setVH = () => this.setViewportHeightMobile();
    this.setVH();

    window.addEventListener("resize", this.setVH);
    window.addEventListener("orientationChange", this.setVH);
  }

  setViewportHeightMobile(innerH = window.innerHeight){
    let vh = innerH * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);

    const windowW = window.innerWidth;
    
    if(windowW > 1024){
      if(
        this.state.visible === "PepehaPortrait" 
        && 
        this.props.state.moveStep.stage === "advanced"
      ){
        this.props.moveStepTo({currStep: "SelectionMenu"});
      } 
    }
    
    this.setState({
      windowW: windowW
    })
  }

  onLoad(){
    setTimeout(() => {
      this.setState({
        visible: "SplashPage"
      })
    }, 250)
  }

  componentDidUpdate(){
    if(this.props.state.moveStep === undefined) return;
    const stepChange = this.props.state.moveStep.currStep;

    if(this.state.visible !== stepChange){
      this.setState({
        visible: stepChange
      })
    }
  }

  componentWillUnmount(){
    this.props.resetPepehaData();
    this.props.resetPosition();
    window.removeEventListener("resize", this.setVH);
    window.removeEventListener("orientationChange", this.setVH);
  }
  
  render() {
    return (
      <React.Fragment>
        <main className={ "-" + this.props.state.moveStep.stage }>
          <div>
            <TopToolbar/>

            <div className="mobileAdjustBox">
              <SplashPage visible={this.state.visible === "SplashPage" ? true : false}/>

              {this.state.visible === "SimpleSteps" ? (
                <SimpleSteps visible={true}/>
              ) : (
                null
              )}
              
              <PepehaPortrait visible={
                (this.state.visible === "PepehaPortrait" || (this.state.windowW > 1024 && this.state.visible === "SelectionMenu")) ? true : false}
              />
            </div>

            <SelectionMenu visible={this.state.visible === "SelectionMenu" ? true : false}/>
          </div>
        </main>

        <PleaseUpdate/>
      </React.Fragment>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(CreatePepeha);