import React, { Component } from 'react';
import { connect } from "react-redux";

import { toolbarChanged, toggleEntryVisibility, moveStepTo } from "../redux/actions";

import FloatingTextInput from '../components/FloatingTextInput';

import '../styles/_selectionMenu.scss';

const mapStateToProps = state => {
  return { state: state };
};

const mapDispatchToProps = dispatch => {
  return {
    toolbarChanged: payload => dispatch(toolbarChanged(payload)),
    toggleEntryVisibility: payload => dispatch(toggleEntryVisibility(payload)),
    moveStepTo: payload => dispatch(moveStepTo(payload)),
  };
}

class SelectionMenu extends Component {
  state = {
    active: false,
    enterText: false,
    dataCount: 0,
    previewing: false
  }

  componentDidUpdate(){
    if(this.state.active !== this.props.visible){
      this.setState({
        active: !this.state.active 
      })

      if(this.props.visible === true){
        this.props.toolbarChanged({buttonColor: 'white', burgerColor: 'white', bcgColor: 'transparent'});
      }
    }

    const dataCount = Object.keys(this.props.state.updatePepehaData).filter(key => this.props.state.updatePepehaData[key].visible === true).length;

    if(this.props.visible === true && this.state.dataCount !== dataCount){
      this.setState({dataCount : dataCount});
    }
  }

  toggleItem(type, entry){
    if(entry !== undefined){
      this.props.toggleEntryVisibility({ name: type });  
    } else {
      this.itemClicked(type);
    }
  }

  itemClicked(type, maori){
    this.props.moveStepTo({ addManually: true });
    this.setState({ enterText: type, enterPlaceholder: maori });
  }

  previewClicked(){
    // this.setState({previewing: true});
    this.props.moveStepTo({ currStep: "PepehaPortrait", prevStep: "SelectionMenu" });
  }

  render() {
    const updatePepehaData = this.props.state.updatePepehaData;

    return (
      <React.Fragment>
        <section className={`selectionMenu ${this.state.active && !this.state.previewing ? '-active' : ''}`}>
          <div className="selectionMenu-explanation">
            <p className="selectionMenu-explanation-text -para1">Complete the relevant layers to create your own personal&nbsp;Pepeha.</p>
          </div>
          <ul className="selectionMenu-list">
            {Object.keys(updatePepehaData).map(type => {
              const item = updatePepehaData[type];

              return(
                <li key={type} className={`selectionMenu-item ${item.visible ? "-active" : ''}`}>
                  <div className="selectionMenu-item-toggle" onClick={() => {this.toggleItem(type, item.entry)}}>
                    <div className="selectionMenu-item-dot">
                      <div className="selectionMenu-item-radio"/>
                    </div>
                  </div>
                  <p onClick={() => {this.itemClicked(type, item.maori)}} className="selectionMenu-item-text">{item.maori}</p>
                </li>
              )
            })}
          </ul>
          <div className={`selectionMenu-preview ctaButton ${this.state.dataCount > 0 ? "-active" : ""}`} onClick={() => this.previewClicked()}>
            <div className="ctaButton-ripple"></div>
            <svg className="ctaButton-button" x="0px" y="0px" viewBox="0 0 73.6 73.6"><circle className="previewSvg" cx="36.8" cy="36.8" r="36.8"/><g className="st1"><path d="M15.1,33.4c0.3,0.1,0.5,0.3,0.8,0.5s0.4,0.5,0.5,0.8c0.1,0.3,0.2,0.6,0.2,0.9s-0.1,0.6-0.2,0.9c-0.1,0.3-0.3,0.6-0.6,0.8
                c-0.2,0.2-0.5,0.4-0.8,0.5s-0.6,0.2-1,0.2h-1.6V41H11v-7.7h3.1C14.5,33.2,14.8,33.3,15.1,33.4z M14.4,36.7c0.2-0.1,0.3-0.1,0.4-0.2
                c0.1-0.1,0.2-0.2,0.3-0.4c0.1-0.1,0.1-0.3,0.1-0.4s0-0.3-0.1-0.4s-0.1-0.3-0.2-0.4c-0.1-0.1-0.2-0.2-0.4-0.2
                c-0.1-0.1-0.3-0.1-0.4-0.1h-1.6v2.2h1.5C14.1,36.8,14.2,36.8,14.4,36.7z"/><path d="M23,41l-1.8-3.2H20V41h-1.4v-7.7h3.2c0.3,0,0.6,0.1,0.9,0.2s0.5,0.3,0.7,0.5c0.2,0.2,0.4,0.4,0.5,0.7
                c0.1,0.3,0.2,0.6,0.2,0.9s-0.1,0.6-0.2,0.9c-0.1,0.3-0.3,0.5-0.5,0.7c-0.2,0.2-0.5,0.4-0.8,0.5l2,3.4H23z M20,36.5h1.6
                c0.3,0,0.6-0.1,0.8-0.3c0.2-0.2,0.3-0.4,0.3-0.7c0-0.1,0-0.3-0.1-0.4c-0.1-0.1-0.1-0.2-0.2-0.3c-0.1-0.1-0.2-0.2-0.3-0.2
                c-0.1-0.1-0.2-0.1-0.4-0.1H20V36.5z"/></g><g className="st1"><path d="M27.8,34.6v1.7h2.7v1.3h-2.7v2.1h3.3V41h-4.7v-7.7h4.7v1.3H27.8z"/></g><g className="st1"><path d="M40,33.2L37,41h-1.1l-3-7.7h1.5l2.1,5.5l2.1-5.5H40z"/><path d="M43.4,41H42v-7.7h1.4V41z"/></g><g className="st1"><path d="M47.5,34.6v1.7h2.7v1.3h-2.7v2.1h3.3V41h-4.7v-7.7h4.7v1.3H47.5z"/></g><g className="st1"><path d="M62.5,33.2L60.3,41h-1.2l-1.6-5.1L56,41h-1.2l-2.3-7.7H54l1.4,5.2l1.5-5.2h1.2l1.5,5.2l1.4-5.2H62.5z"/></g></svg>
          </div>
        </section>

        <FloatingTextInput 
          visible={this.props.state.moveStep.addManually && this.props.state.moveStep.stage !== "simple"} 
          category={this.state.enterText} 
          placeholder={this.state.enterPlaceholder}
        />
      </React.Fragment>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(SelectionMenu);