import axios from 'axios';
import { detect } from 'detect-browser';

export default function uploadData(userData, portraitData){
  const postPortrait = portraitData => {
    return new Promise((resolve, reject) => {
      axios({
        method: "post",
        url: 'https://ng51uya03l.execute-api.us-west-2.amazonaws.com/pepeha',
        timeout: 1000 * 10, // Wait for 10 seconds
        headers: {
          "Content-Type": "application/json"
        },
        data: {
          pdfString: btoa(portraitData.pdf),
          jpgString: portraitData.img.split(',')[1]
        }
      }).then(response => {
        if(response.data.dialogAction.fulfillmentState === "Fulfilled"){
          resolve(response);
        } else {
          reject(response.data.dialogAction);
        }
      }).catch(err => {
        reject(err);
      });
    })
  }

  const postUserData = (userData, response) => {
    return new Promise((resolve, reject) => {
      const splitLocation = response.data.dialogAction.message.content.pdf.split("/");
      const pdfId = splitLocation[1],
            pdfName = splitLocation[3].split(".")[0];

      document.cookie = `dataAW=${pdfId},${pdfName};path=/`;

      let device = detect();

      let formData = {
        ...userData,
        "jpg": "https://s3-ap-southeast-2.amazonaws.com/pepeha-dev/" + response.data.dialogAction.message.content.jpg,
        "pdf": "https://s3-ap-southeast-2.amazonaws.com/pepeha-dev/" + response.data.dialogAction.message.content.pdf,
        "browser": device.name,
        "os": device.os,
        "browserVersion": device.version,
        "userAgent": navigator.userAgent
      }

      resolve("https://s3-ap-southeast-2.amazonaws.com/pepeha-dev/" + response.data.dialogAction.message.content.pdf);

      // axios({
      //   method: "post",
      //   url: './cdkckdjcndfk.php',
      //   timeout: 1000 * 10, // Wait for 10 seconds
      //   headers: {
      //     "content-type": "application/x-www-form-urlencoded",
      //     'Access-Control-Allow-Origin': '*'
      //   },
      //   data: {
      //     "formData": JSON.stringify(formData)
      //   }
      // }).then(response => {
      //   console.log(response);
      //   if(response === "New record created successfully"){
      //     resolve('success! Save email');
      //     window.location = "./download-ready.html";
      //   } else {
      //     reject('email not saved');
      //   }
      // }).catch(err => {
      //   reject(err);
      // });
    });
  }

  return new Promise((resolve, reject) => {
    postPortrait(portraitData)
    .then(response => {
      // console.log('response',response);
      postUserData({...userData, trackLines: portraitData.trackLines}, response)
      .then(response => {
        // console.log('response',response);
        resolve(response);
      })
      .catch(err => {
        reject(err);
      })
    })
    .catch(err => {
      reject(err);
    })
  })
}