import React, { Component } from 'react';
import { connect } from "react-redux";
import { moveStepTo, toolbarChanged, resetPepehaData, resetExplanations } from "../redux/actions";

import '../styles/_splashPage.scss';

const mapDispatchToProps = dispatch => {
  return {
    moveStepTo: payload => dispatch(moveStepTo(payload)),
    toolbarChanged: payload => dispatch(toolbarChanged(payload)),
    resetPepehaData: payload => dispatch(resetPepehaData(payload)),
    resetExplanations: payload => dispatch(resetExplanations(payload)),
  };
}

class SplashPage extends Component{
  state = {
    active: false
  }

  componentDidMount(){
    console.log('window.innerWidth',window.innerWidth);
    
  }

  componentDidUpdate(){
    if(this.state.active !== this.props.visible){
      let burgerColr = 'black';

      if(window.innerWidth <= 1024) burgerColr = 'white';

      if(this.props.visible === true){
        this.props.toolbarChanged({buttonColor: 'white', button: 'close', burgerColor: burgerColr})
      }

      this.setState({
        active: !this.state.active 
      })
    }
  }

  // Previously simpleadvanced
  render() {
    return (
      <section className={`splashPage ${this.state.active ? '-active' : ''}`}>
        <div className="splashPage-container">
          <div 
            className="splashPage-advanced" 
            onClick={() => {
              this.props.moveStepTo({stage: 'advanced', currStep: 'SelectionMenu', prevStep: 'SplashPage'})
              this.props.toolbarChanged({burgerColor: 'white', button: 'back'})
            }}
          >
            <h3 className="splashPage-advanced-heading">I Know<br/>My Pepeha</h3>
            <p className="splashPage-advanced-description -para1">Choose this option for those who can easily write their own Pepeha.</p>
          </div>
          <div 
            className="splashPage-simple"
            onClick={() => {
              this.props.moveStepTo({stage: 'simple', currStep: 'SimpleSteps', prevStep: 'SplashPage'})
              this.props.toolbarChanged({buttonColor: 'black', button: 'back', burgerColor: 'black'})
              this.props.resetPepehaData()
              this.props.resetExplanations()
            }}
          >
            <h3 className="splashPage-simple-heading">Learn your<br/>Pepeha</h3>
            <p className="splashPage-simple-description -para1">Choose this option for those who need help to create their own Pepeha.</p>
          </div>
        </div>
      </section>
    );
  }
}

export default connect(null, mapDispatchToProps)(SplashPage);